body {
    overflow-x: hidden;
}

.first-screen {
    margin-bottom: 97px;
    .container-xxl {
        width: 100%;
        max-width: 1820px;

        .container {
            .title-slider {
                margin-top: 163px;
                max-width: 463px;
                font-size: 56px;
                font-style: normal;
                font-weight: 700;
                text-transform: uppercase;
                line-height: 135.5%;
                margin-bottom: 0;
                line-height: 1.3;
                /* 27.1px */
            }

            .subtitle {
                margin-bottom: 42px;
                font-size: 20px;
                font-style: normal;
                font-weight: 300;
                height: 54px;
                max-width: 345px;
                line-height: 1.5;
            }

            .subtitle-1 {
                max-width: 446px;
            }


            .btn {
                margin-bottom: 228px;

                .icon {
                    margin: 0 -14px 0 34px;
                }
            }
        }

        .first-screen-nav {
            justify-content: space-between;
            display: flex;
            position: absolute;
            max-width: 1538px;
            width: 100%;
            top: 50%;
            left: 50%;
            padding: 0 8px;
            transform: translate(-50%, -50%);

            .slick-arrow {
                width: 46px;
                height: 46px;
            }
        }

        .slick-dots {
            display: flex;
            justify-content: center;
            position: absolute;
            bottom: 44px;
            left: 50%;
            transform: translate(-50%, 0px);
        }

        .slider {
            border-radius: 20px;
            max-width: 1820px;
            width: 100%;
            height: 700px;
            background-repeat: no-repeat;
            background-size: cover;
        }
    }
}
