@media screen and (max-width: 1300px) {
    header{
        .navbar-light {
            .navbar-nav {
                margin: 5px auto 0;
                max-width: 560px;
            }
        } 
    }
    .first-screen{
        .test{
            .slider-box{
                .slider{
                    padding-left: 46px;
                    background-position: center;
                }
            }
        }
    }
    .delivery , .services{
        .container-carousel{
            .slick-list{
                max-width: 720px;
                margin: 0 auto;
            }
        }
        .arows-card{
            .slick-prev{
                transform: translate(0, -50%) rotate(180deg);
            }
            .slick-next{
                transform: translate(0, -50%);
            }
        }
    } 
}
