.services{
    margin-bottom: 98px;
    .template-title-type2{
        margin-bottom: 77px;
    }
    .services-container{
        position: relative;
        border-radius: 20px;
        padding-bottom: 100px;
        .services-title{
            padding-top: 90px;
        }
        .template-slider{
            .card{
                max-width: 360px;
                height: 482px;
                border-radius: 20px;
                border: none;
                margin: 0 15px;
                background-color: #363A4D;
                .card-img{
                    position: absolute;
                    width: 100%;
                    height: 100%;
                    background-repeat: no-repeat;
                    background-size: cover;
                    border-radius: 20px;
                }
                .card-img::before{
                    content: "";
                    position: absolute;
                    right: 0;
                    left: 0;
                    bottom: 0;
                    top: 0;
                    background: linear-gradient(0deg, rgba(4, 5, 17, 0.91) 0%, rgba(88, 88, 88, 0.00) 100%);
                    border-radius: 20px;
                }
                .card-content{
                    height: 100%;
                    z-index: 2;
                    position: relative;
                    padding: 0 23px 33px 30px;
                    display: flex;
                    justify-content: flex-end;
                    flex-direction: column;
                    .card-description{
                        p{
                            font-size: 15px;
                            font-weight: 400;
                            line-height: 124.5%
                        }
                        .btn{
                            padding: 13px 0;
                            justify-content: center;
                            max-width: 153px;
                            width: 100%;
                        }
                    }
                }
            }
        }
    }
    .services-container::before{
        content: "";
        position: absolute;
        top: 0;
        bottom: 0;
        left: 50px;
        right: 50px;
        background-color: #363A4D;
        border-radius: 20px;
        z-index: -1;
    }
    .slick-list {
        margin: 0 -15px;
    }
}