.delivery{
    margin-bottom: 93px;
    .container-xxl{
        .card{
            background-color: transparent;
            border: none;
            padding: 0 15px;
            max-width: 390px;
            .card-img{
                width: 100%;
                height: 229px;
                border-radius: 20px 20px 0 0;
                background-position: center;
                background-repeat: no-repeat;
                background-size: cover;
                img{
                    border-radius: 20px 20px 0 0;
                }
            }
            .card-content{
                background-color: #363A4D;
                justify-content: center;
                flex-direction: column;
                align-items: center;
                display: flex;
                border-radius: 0 0 20px 20px;
                text-align: center;
                padding: 0 20px;
                .card-title{
                    margin-top: 26px;
                    line-height: 124.5%;
                    margin-bottom: 12px;
                }
                .card-description{
                    width: 100%;
                    .row-card{
                        .col-6{
                            display: flex;
                            align-items: start;
                            padding: 0;
                            padding-left: 19px;
                        }
                        margin: 0 auto;
                        max-width: 250px;
                        width: 100%;
                        .child-card{
                            img{
                                max-width: 24px;
                                width: 100%;
                            }
                            p{
                                padding-left: 8px;
                                margin-bottom: 14px;
                            }
                        }
                    }
                    .price-card{
                        font-size: 45px;
                        font-weight: 700;
                        line-height: 124.5%;
                        margin-bottom: 9px;
                    }
                    .price-card span:first-child{
                        font-size: 22px;
                        font-weight: 400;
                        color: #A3A3A3;
                    }
                    .price-card span:last-child{
                        font-size: 22px;
                        font-weight: 400;
                    }
                }
                .card-button{
                    gap: 12px;
                    margin: 0 0 27px;
                    display: flex;
                    width: 100%;
                    .btn{
                        width: 100%;
                        padding: 14px 0;
                        justify-content: center;
                    }
                }
            }
        }
        .slick-list {
            margin: 0 -15px;
        }
    }
    .row{
        margin: 0;
    }
}