.advantages{
    .advantages-container{
        position: relative;
    }
    .advantages-container::before{
        content: "";
        position: absolute;
        top: 0;
        bottom: 0;
        left: 50px;
        right: 50px;
        background-color: #363A4D;
        border-radius: 20px;
        z-index: 1;
    }
    .advantages-container .container{
        z-index: 2;
        position: sticky;
        .advantages-title{
            margin-top: 90px;
            margin-bottom: 17px;
        }
        .advantages-parent{
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
        }
        .icon-img{
            padding: 10px;
            border: 1px solid #6C7188;
            border-radius: 50%;
            overflow: hidden;
            margin-bottom: 22px;
        }
        .icon-img img{
            height: 93px;
            max-width: 93px;
            object-fit: scale-down;
            width: 100%;
            background-color: #89C249;
            border-radius: 50%;
        }
        .advantages-subtitle{
            text-align: center;
            font-size: 18px;
            font-weight: 400;
            line-height: 124.5%;
            padding: 0 50px;
            margin-bottom: 48px;
        }
    }
    .slider-box_advantages{
        padding-bottom: 63px;
    }
}