.calculation {
    margin-bottom: 111px;
    .check-box_calculation:nth-child(2n - 1) {
        padding-top: 23px;
        margin-bottom: 15px;
    }

    .container {
        .title-calculation h2 {
            font-size: 36px;
            font-weight: 700;
            line-height: 124.5%;
            /* 44.82px */
            margin: 0;
            display: inline-block;
            max-width: 610px;
            width: 100%;
            text-transform: uppercase;
        }
        .title-calculation{
            margin-bottom: 59px;
        }
        .title-calculation h2::after {
            content: "";
            display: inline-block;
            max-width: 85px;
            width: 100%;
            height: 2px;
            background-color: #89C249;
        }

        .container-left_form {
            background-color: #363A4D;
            border-radius: 20px;
            padding: 48px 43px 61px 50px;

            .form-parent {
                .custom-input {
                    width: 100%;
                    height: 55px;
                    font-size: 15px;
                    border-radius: 10px;
                    border: none;
                    padding-left: 20px;
                    font-weight: 400;
                    margin-top: 6px;
                    line-height: 126%;
                }

                .custom-input:hover {
                    box-shadow: 0px 0px 4px 1px #89C249;
                }

                .custom-input:focus {
                    box-shadow: 0px 0px 4px 1px #89C249;
                }

                .custom-input:focus-visible {
                    outline: none;
                }

                [type=text],
                [type=number] {
                    margin-bottom: 15px;
                }

                .title-input {
                    font-weight: 600;
                    font-size: 15px;
                    line-height: 126%;
                    width: 100%;
                    text-decoration: none;
                    color: #FFF;
                }
            }

            .btn {
                margin-top: 25px;
                height: 68px;
                padding: 0;
                padding-left: 32px;
                width: 100%;
                letter-spacing: 0;

                .icon {
                    margin: 0 -14px 0 50px;
                }
            }
        }

        .container-img_calculation {
            max-width: 560px;
            width: 100%;
            position: absolute;
            top: -177px;
            right: -6px;
        }

        #file-1 {
            display: none;
        }

        .download-file {
            padding: 11px 86px 10px;
            max-width: 526px;
            width: 100%;
            border: 2px dashed rgb(255 255 255 / 26%);
            text-align: center;
            border-radius: 10px;
            cursor: pointer;
            font-size: 15px;
            font-weight: 400;
        }

        .download-file:hover {
            box-shadow: 0px 0px 10px 1px #89C249;
            transition: all 100ms;
            color: #89C249;
        }

        #file-name {
            margin-top: 17px;
            margin-bottom: 12px;
        }
        .file-name{
            font-size: 15px;
            font-weight: 400;
        }
    }
}
