@media screen and (max-width: 991px) {
    header {
        .navbar {
            margin-top: 17px;
            margin-bottom: 15px;

            .img-logo {
                padding-left: 20px;
            }

            .img-logo img {
                width: 126px;
                height: 35px;
            }

            button.hamburger.hamburger--squeeze.js-hamburger {
                display: inline-block;
                width: 27px;
                height: 25px;
                margin-bottom: 15px;
                margin-right: 20px;

                .hamburger-box {
                    height: 25px;
                }

                .hamburger-inner,
                .hamburger-inner::before,
                .hamburger-inner::after {
                    width: 27px;
                    height: 2.5px;
                }

                span.hamburger-inner {
                    top: 21px;
                }
            }

            .navbar-collapse {
                background-color: #363A4D;
                max-height: 508px;
                box-shadow: 14px 22px 40px 0px rgba(30, 35, 66, 0.28);

                .navbar-nav {
                    height: 309px;
                    margin: auto;
                    margin-top: 52px;
                    margin-bottom: 34px;
                    justify-content: space-between;
                }

                .d-flex {
                    margin-bottom: 60px;
                    justify-content: center;

                    .btn {
                        padding: 22px 80px 21px;
                        font-size: 16px;
                    }
                }
            }


            #navbarSupportedContent {
                .navbar-nav {
                    .nav-item {
                        .nav-link {
                            font-size: 24px;
                        }
                    }
                }
            }
        }
    }

    .first-screen {
        margin-bottom: 55px;

        .container-xxl {
            padding: 0;

            .test {
                .slider-box {
                    .slider {
                        padding: 0;
                        background-position: center;
                        background-size: cover;
                        height: calc(100vh - 72px);
                        border-radius: 0;
                        display: flex;

                        .container {
                            text-align: center;
                            align-items: center;
                            justify-content: center;
                            display: flex;
                            flex-direction: column;
                            position: relative;
                            z-index: 2;

                            .title-slider {
                                max-width: 290px;
                                margin-bottom: 10px;
                                width: 100%;
                                height: 80px;
                                font-size: 32px;
                                line-height: 126%;
                                /* 40.32px */
                            }

                            .subtitle {
                                max-width: 335px;
                                width: 100%;
                                height: 44px;
                                font-size: 16px;
                                line-height: 135.5%;
                                /* 21.68px */
                                margin-bottom: 18px;
                            }
                        }
                    }

                    .slick-slide:before {
                        content: "";
                        position: absolute;
                        z-index: 1;
                        top: 0;
                        right: 0;
                        bottom: 0;
                        left: 0;
                        background: rgb(0 0 0 / 5%);
                    }

                    .slick-dots {
                        left: 50%;
                        transform: translate(-50%, 50%);
                    }
                }

                /*.first-screen-nav {
                    display: none;
                }*/
            }
        }
    }

    .calculation {
        margin-bottom: 60px;

        .container {
            .box-left_form {
                padding: 0 24px;
            }

            .container-left_form {
                padding: 45px 30px 32px 31px;
            }

            .title-calculation {
                text-align: center;
                max-width: 100%;
                font-size: 24px;
                font-weight: 700;
                line-height: 124.5%;
                padding-bottom: 23px;
                position: relative;

                h2 {
                    max-width: 330px;
                    text-align: center;
                    font-size: 24px;
                    font-style: normal;
                    font-weight: 700;
                    text-transform: uppercase;
                }
            }

            .title-calculation {
                ::after {
                    position: absolute;
                    left: 50%;
                    transform: translate(-50%, 0);
                    top: calc(100% - 2px);
                    margin: 0;
                }
            }

            .container-img_calculation {
                display: none;
            }

            .custom-input {
                max-width: 100%;
            }

            .check-box_calculation {
                padding-top: 15px;
            }

            [type=checkbox]+label:before,
            [type=radio]+label:before {
                width: 27px;
                height: 23px;
            }

            .container-checkbox {
                margin-bottom: 13px;
            }

            #file-name {
                margin-top: 5px;
                margin-bottom: 11px;
            }

            .download-file {
                padding: 9px 20px 8px;
                max-width: 100%;
            }

            .container-left_form .btn {
                margin-top: 27px;
                height: 68px;
                position: relative;

                .icon {
                    margin-right: 17px;
                    position: absolute;
                    right: 0;
                }
            }
        }
    }

    .advantages {
        .advantages-container {
                .advantages-title {
                    font-size: 24px;
                    max-width: 310px;
                    margin: 55px auto 17px auto;
                }

            .slider-box_advantages {
                padding-bottom: 60px;

                .advantages-subtitle {
                    max-width: 270px;
                    margin: 0 auto 48px;
                }
            }
        }

        .advantages-container::before {
            left: 0;
            right: 0;
            border-radius: 0;
        }
    }


    .delivery,
    .services {
        .container-carousel {
            .slick-list {
                max-width: 360px;
                margin: 0 auto 32px;
            }
        }
        .template-title{
            font-size: 24px;
        }
        .services-title{
            font-size: 24px;
        }
    }

    .services {
        .services-container::before {
            content: "";
            position: absolute;
            top: 0;
            bottom: 0;
            left: 0;
            right: 0;
            background-color: #363A4D;
            border-radius: 0;
            z-index: -1;
        }
    }

    .about-company {
        .container {
            .col-lg-6 {
                .about-company-title {
                    text-align: center;
                    margin: 0 auto 45px;
                    font-size: 24px;
                }

                .about-company-title::after {
                    left: 50%;
                    transform: translate(-50%, 0);
                }

                .about-company-descrition {
                    margin: 0 auto 23px;
                    font-size: 16px;
                    font-weight: 300;
                }
                .about-company-descrition:last-child{
                    margin-bottom: 40px;
                }

                .about-company_img-container {
                    transform: translate(0, 0);
                    max-width: 277px;
                    height: 277px;
                    margin: 0 auto;

                    img {
                        transform: translate(-30px, 0);
                        max-width: 242px;
                        height: 284px;
                    }
                }
            }
        }
    }
}