@media (min-width: 992px) {
    header {
        .navbar {
            .container {
                .d-flex {
                    .btn {
                        padding: 12px 22px;
                    }
                }
            }
        }
    }

    .first-screen-slider {
        .slick-slide {
            padding: 0 92px;
            border-radius: 20px;
        }

        .container {
            padding-top: 164px;
            padding-bottom: 228px;
        }
        .template-title {
            &.col-lg-7 {
                text-align: left;
    
                &:after {
                    display: none;
                }
            }
        }
    
        .input-row {
            display: flex;
        }
    }

    .calculation{
        .container{
            .box-left_form {
                max-width: 650px;
                width: 100%;
            }
            .title-calculation h2::after{
                margin: 0px 0px 9px 19px;
            }
            .custom-input{
                max-width: 248px;
            }
        }
    }
}
