.about-company{
    margin-bottom: 50px;
    padding-top: 57px;
    .container{
        .col-lg-6{
            .about-company-title{
                max-width: 250px;
                font-size: 36px;
                line-height: 124.5%;
                text-align: start;
                text-transform: uppercase;
                margin-bottom: 45px;
            }
            .about-company-title::after{
                left: 0;
                transform: translate(0, 0);
            }
            .about-company-descrition {
                font-size: 18px;
                font-weight: 300;
                line-height: 170%;
                margin-bottom: 23px;
                max-width: 555px;
            }
        }
        .about-company_img-container {
            background-color: #363A4D;
            border-radius: 50%;
            transform: translate(0px, -57px);
            padding: 0 44px;
            max-height: 550px;
            img{
                transform: translate(-10px, 0px);
                max-width: 488px;
            }
        }
        
    }
}