header {
    .navbar {
        margin: auto;
        max-width: 1140px;
        height: 100%;
        margin-top: 25px;
        margin-bottom: 39px;
        padding: 0;

        .container {
            padding: 0;

            .hamburger {
                display: none;
            }

            .navbar-collapse {
                justify-content: space-between;
                max-width: 890px;

                .navbar-nav {
                    text-align: center;
                    max-width: 640px;
                    width: 100%;

                    .nav-item {
                        margin: auto;
                        margin-top: 16px;
                        margin-bottom: 11px;

                        .nav-link {
                            color: #FFF;
                            font-family: ProximaNova;
                            font-weight: 400;
                            font-size: 16px;
                            line-height: 23.36px;
                            padding: 0;
                        }

                        .nav-link:hover {
                            color: #89C249;
                        }
                    }
                }

                .d-flex {
                    text-align: center;

                    .btn {
                        display: block;

                    }
                }
            }
        }
    }
}
